$by-pagination-color: #999;

.by-pagination {
  display: inline-block;
  padding-left: 0;
}

.by-pagination li {
  display: inline;
}

.by-pagination li a {
  color: $by-pagination-color;
  float: left;
  font: 12px/1.5 Microsoft YaHei,tahoma,arial,"Hiragino Sans GB",\5b8b\4f53;
  margin: 0 6px;
  padding: 2px 4px;
  position: relative;
  text-decoration: none;
}

.by-pagination li a:hover,
.by-pagination li a:focus {
  border-color: #ccc;
  color: #ee7326;
}

.by-pagination li span {
  float: left;
  margin: 0 2px;
  position: relative;
}

.by-pagination li.active a {
  border-color: $by-pagination-color;
  color: #ee7326;
}

.by-pagination li a.disabled {
  background-color: #f3f3f3;
  border-color: #ccc;
  color: #ccc;
  cursor: not-allowed;
}

.by-pagination .prev-next a{
	border: 1px solid #dedede;
	padding: 2px 6px;
}

